// Colors.
$color_transparent: rgba(0, 0, 0, 0);
$color_white: #ffffff;
$color_black: #000000;
$color_accent: #19110b;
$color_accent2: #19110b;
$color_bg: #ffffff;
$color_text: #666666;
$color_remove: #fb565e;
$color_heading: #333333;
$color_subtitle: rgba(0, 0, 0, 0.05);
$color_border: rgba(0, 0, 0, 0.1);


// Responsive Breakpoints.
$breakpoint_lg: 1024px;
$breakpoint_md: 768px;
$breakpoint_sm: 500px;

$breakpoint_wordpress: 783px;

// WordPress.
$adminbar_height: 32px;
$adminbar_height_mobile: 46px;

// Popup.
$popup_gutter: 30px;
$popup_side_width: 40vw;
$popup_center_width: 60vw;
$popup_side_width_md: 50vw;
$popup_center_with_md: 80vw;
$popup_side_width_sm: 98vw;
$popup_center_width_sm: 95vw;

// Spacing.
$spacing_lg: 4em;
$spacing_md: 2em;
$spacing_sm: 1em;
$spacing_md_lg: 3em;
$spacing_md_md: 1.5em;
$spacing_md_sm: 1em;
$spacing_sm_lg: 2em;
$spacing_sm_md: 1.2em;
$spacing_sm_sm: 1em;
